import { useState } from 'react';
import { helpHttp } from '../helpers/HelpFetch';


export function UseForm( inicialForm, validateForm ) {
    const [form, setForm] = useState(inicialForm);
    const [error, setError] = useState({});
    const [loading, setLoading] = useState(false);
    const [res, setRes] = useState(null);

    const handleChange = (e) => {
        const { name, value } = e.target;
        setForm({
            ...form,
            [ name ] : value
        })
    }

    const handleBlur = (e) => {
        handleChange(e);
        setError(validateForm( form , e ));
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        setError(validateForm( form ));

        if (Object.keys(error).length === 0) {
            setLoading(true);
            helpHttp()
                .post('https://formsubmit.co/ajax/fycimpresion@gmail.com',{
                    body: form,
                    headers: {
                        "Content-Type":'application/json',
                        Accept:'application/json'
                    },
                })
                .then((res) => {
                    setLoading(false);
                    setRes(true);
                    setForm(inicialForm);
                });
        } else {
            return;
        }
    }

    return {
        form,
        error,
        res,
        loading,
        handleChange,
        handleBlur,
        handleSubmit
    }
};
