import { React , useEffect, useRef, useState } from "react";
import { Link, useLocation } from "react-router-dom";
import Logo from "../recursos/logo-ppal.png"
import './Navegador.scss'

function Navegador() {
    let Mostrar = useRef(),
        Cerrar = useRef(),
        Oculto = useRef(),
        home = useRef(),
        galeria = useRef(),
        contact = useRef(),
        about = useRef();

    let page = useLocation()
    const [location, setLocation] = useState();

    useEffect(() => {
        setLocation(location)
        page.pathname==='/'?home.current.classList.add('active')
        :home.current.classList.remove('active')
        page.pathname==='/Galeria'?galeria.current.classList.add('active')
        :galeria.current.classList.remove('active')
        page.pathname==='/Contacto'?contact.current.classList.add('active')
        :contact.current.classList.remove('active')
        page.pathname==='/Conocenos'?about.current.classList.add('active')
        :about.current.classList.remove('active')
    }, [location,page.pathname]);

    function Location(e) {
        setLocation(page.pathname);
    }

    const AbrirMenu = (e) => {
        Oculto.current.style.display = 'flex'
        document.body.style.overflow = 'hidden'
        setTimeout(() => {
            Oculto.current.style.transform = 'translate(0%)'
        }, 100);
    }

    const CerrarMenu = (e) => {
        Oculto.current.style.transform = 'translate(100%)'
        document.body.style.overflow = 'visible'
        setTimeout(() => {
            Oculto.current.style.display = 'none'
        }, 1000);
    }

    return ( 
        <>
        <div className="nav" id="nav" >
            <Link className="logo" to={'/'} >
                <img src={Logo} alt=""/>
            </Link>
            <div className="navegacion">
                <Link onClick={Location} className="link" ref={home} to={'/'}>Inicio</Link>
                <Link onClick={Location} className="link" ref={galeria} to={'/Galeria'}>Galerìa</Link>
                <Link onClick={Location} className="link" ref={contact} to={'/Contacto'}>Contacto</Link>
                <Link onClick={Location} className="link" ref={about} to={'/Conocenos'}>Conòcenos</Link>
            </div>
            <div className="oculto">
                <span onClick={AbrirMenu} ref={Mostrar} className="material-symbols-outlined bars">
                    menu
                </span>
            </div>
        </div>
        <div ref={Oculto} className="menu-oculto">
            <Link className="link-rsp" onClick={CerrarMenu} to={'/'}>Inicio</Link>
            <Link className="link-rsp" onClick={CerrarMenu} to={'/Galeria'}>Galerìa</Link>
            <Link className="link-rsp" onClick={CerrarMenu} to={'/Contacto'}>Contacto</Link>
            <Link className="link-rsp" onClick={CerrarMenu} to={'/Conocenos'}>Conòcenos</Link>
            <span ref={Cerrar} onClick={CerrarMenu} className="close material-symbols-outlined">
                close
            </span>
        </div>
        </>
    );
}

export default Navegador;