import './Card.css'
import Swal from 'sweetalert2'
import { Zoom } from 'react-reveal';


function Video(props) {
    
    function handleClick(e){
        Swal.fire({
            title: 'Grafica | FyC',
            html: `<video class="video-max" controls id=${props.id} src=${props.video}/>`,
            text: `${props.titulo}`,
            imageWidth: '300px',
            imageHeight: '400px',
            confirmButtonText: 'Cerrar',
            confirmButtonColor: '#4558BD'
        });

}
    return (  
        <Zoom>
            <div className='card'>
                <video className='video' controls id={props.id} src={props.video}/>
                <section className='describ'>
                    <h4 className='titulo-img'>{props.titulo}</h4>
                    <p className='fecha-img'>{props.fecha}</p>
                    <p className='max' onClick={handleClick} >Maximizar</p>
                </section>
            </div>
        </Zoom>
    );
}

export default Video;