import { useEffect, useRef } from "react";
import { Link } from "react-scroll";


function Uper() {
    
    let uper = useRef();
    
    
    useEffect(() => {


        const handleScroll = e => {
            window.scrollY>300
                ?uper.current.classList.add('uper-visible')
                :uper.current.classList.remove('uper-visible')
        };
        window.addEventListener('scroll', handleScroll);
      }, []);

    return (  
        <Link 
            to="nav"
            smooth="true"
            duration={600}
        >
            <div ref={uper} className="uper">
                <span className="icon material-symbols-outlined">
                    keyboard_double_arrow_up
                </span>
            </div>
        </Link>
    );
}

export default Uper;