import './Portada.css'
import wsp2 from '../recursos/wsp2.png'
import Logo from "../recursos/Logo-footer.png"
import portada from '../recursos/img-portada.png'
import { Bounce } from 'react-reveal';

function Portada() {
    return (  
        <div className="imagen">
            <div className='cont-img'>
                <img className='portada-img' loading='lazy' src={portada} alt='grafica-fyc' />
            </div>
            <div className='clip'></div>
            <div className='centro'>
                <Bounce><img className='img' src={Logo} loading='lazy' alt='logo, marca,grafica'/></Bounce>
                <h4 className='h4'>Comunicate con nosotros</h4>
                <h5 className='h5'>Nos podes escribir en cualquier momento</h5>
                <div className='telefonos'>
                    <img className='wsp' src={wsp2} alt='call , whatsapp'/>
                    <p className='p'><b>113884-9632 Cesar | 1165250943 Fernando</b></p>
                </div>
            </div>
        </div>
    );
}

export default Portada;