import worker from '../recursos/worker.png'
import diseño from '../recursos/diseño.png'
import printer from '../recursos/print.png'
import ex1 from '../recursos/ejemplo1.jpeg'
import ex2 from '../recursos/ejemplo2.jpeg'
import ex3 from '../recursos/ejemplo3.jpeg'
import Portada from './Portada'
import './inicio.scss'
import { Fade, Zoom } from 'react-reveal'

function Inicio() {
    return ( 
        <div className="home">
            <Portada/>
            <div className='contenido'>
                <Fade left big cascade><h2>NOVEDADES!</h2></Fade>
                <div className='news'>
                    <Zoom>
                    <img src={ex1} alt='news , works'/>
                    <img src={ex2} alt='news , works'/>
                    <img src={ex3} alt='news , works'/>
                    </Zoom>
                </div>
                <Fade right big cascade><h2>NOSOTROS:</h2></Fade>
                <div className='work-mode'>
                    <Zoom bottom>
                        <div className='mode'>
                            <img src={diseño} alt='work,mode'/>
                            <p>DISEÑAMOS</p>
                        </div>
                        <span className='line'></span>
                        <div className='mode'>
                            <img src={printer} alt='work,mode'/>
                            <p>IMPRIMIMOS</p>
                        </div>
                        <span className='line'></span>
                        <div className='mode'>
                            <img src={worker} alt='work,mode'/>
                            <p>COLOCAMOS</p>
                        </div>
                    </Zoom>
                </div>
            </div>
        </div>
    );
}

export default Inicio;