import { Flip } from 'react-reveal';
import './Contacto.scss'
import Formulario from './Formulario';

function Contacto() {
    return (  
        <div>
            <Flip top><h2 className="title-contact">Esperamos tu Mensaje</h2></Flip>
            <div className="details">
                <Flip right cascade><div className="container-form">
                    <Formulario/>
                </div>
                </Flip>
                <Flip left cascade>
                <div className="conteiner-map">
                    <iframe title='mapa' loading='lazy' src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d1637.3705782055938!2d-58.28879524786076!3d-34.83760002326666!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x95a32a2f3f0063c1%3A0xdbc0ea69f38fb33f!2sGrafica%20FYC!5e0!3m2!1ses-419!2sar!4v1658239151680!5m2!1ses-419!2sar"></iframe>
                    <div className="info">
                        <ul>
                            <li>Email: <a className='grafica' href='mailto:fycimpresion@gmail.com'>GraficaFyC</a></li>
                            <li>Cesar: <a className='tel' href='tel:0541138849632'>11-3884-9632</a></li>
                            <li>Fernando: <a className='tel' href='tel:0541127516798'>11-6525-0943</a></li>
                            <li>Buenos Aires / Argentina</li>
                        </ul>
                    </div>
                </div>
                </Flip>
            </div>
        </div>
    );
}

export default Contacto;