import React from 'react';
import './Formulario.scss';
import swal from 'sweetalert';
import { UseForm } from './UseForm';
import loader from '../recursos/loaders/grid.svg'
import { Flip } from 'react-reveal';

function Loader() {
    return (  
        <img style={{width:'35px',margin:'auto'}} alt='loader' src={loader}/>
    );
}

const inicialForm = {
    nombre: '',
    email: '',
    coments: ''
}

const validateForm = ( form, e ) => {
    let error = {};
    let regexNombre = /^[A-Za-zÑñÁáÉéÍíÓóÚúÜü\s]+$/,
        regexEmail = /^[a-z0-9]+(\.[_a-z0-9]+)*@[a-z0-9-]+(\.[a-z0-9-]+)*(\.[a-z]{2,15})$/,
        regexComents = /^.{1,255}$/;

    if (e.target.name==='nombre') {
        if (!form.nombre.trim()) {
            error.nombre = 'El campo de Nombre es requerido'
        } else if (!regexNombre.test(form.nombre.trim())) {
            error.nombre = 'El campo de Nombre solo acepta letras y espacios en blanco'
        }
    }
    if (e.target.name==='email') {
        if (!form.email.trim()) {
            error.email = 'El campo de Email es requerido'
        }else if (!regexEmail.test(form.email.trim())) {
            error.email = 'Ingrese un email valido para enviar'
        }
    }
    if (e.target.name==='coments') {
        if (!form.coments.trim()) {
            error.coments = 'Escriba un mensaje para enviar'
        }else if (!regexComents.test(form.coments.trim())) {
            error.coments = 'No se pueden enviar mensajes de mas de 255 caracteres.'
        }
    }

    return error;
};

function Formulario() {
    const {
        form,
        error,
        res,
        loading,
        handleChange,
        handleBlur,
        handleSubmit
    } = UseForm(inicialForm, validateForm)

    return (<Flip right cascade><div className='conteiner-form'>
        <form onSubmit={handleSubmit}>
            <input 
                type='text' 
                name='nombre' 
                placeholder='Nombre:' 
                onBlur={handleBlur}
                onChange={handleChange}
                value={form.nombre}
                required 
                />
            {error.nombre && <p className='error-input'>{error.nombre}</p>}
            <input 
                type='email' 
                name='email' 
                placeholder='Email:'
                onBlur={handleBlur}
                onChange={handleChange}
                value={form.email}
                required 
                />
            {error.email && <p className='error-input'>{error.email}</p>}
            <textarea 
                name='coments' 
                placeholder='Mensaje:'
                onBlur={handleBlur}
                onChange={handleChange}
                value={form.coments}
                required 
                />
            {error.coments && <p className='error-input'>{error.coments}</p>}
            <input 
                type='submit' 
                value='Enviar'
                onSubmit={handleSubmit}
                />
            {loading && <Loader/>}
            {res && swal("Perfecto!",
             "Su mensaje ha sido enviado con exito", "success")}
        </form></div></Flip>
    );
}

export default Formulario;