import React, { useEffect, useRef, useState } from 'react';
import { Link } from 'react-scroll';
import axios from 'axios';
import Data from './Feed';
import './Galeria.scss'


function Peticion () {

    let token = 'IGQVJYODYtWUh2cEpBY3pRd0dBdUZAoRkxpU3ljT0kxSWlnTDBNbzh4c1VIb0loRm5rRHVVUWx6X3NPTWpDUkFNYVMtOEpDcjBuM1dmbGhQeVFjbFM1R3dRYzBFWWh3RWpVdm12blloZAnFJaTNhYjdmZAwZDZD';
    let FeedInstagram = `https://graph.instagram.com/me/media?fields=thumbnail_url,media_url,children,children_url,timestamp,media_type,caption,permalink&limit=10&access_token=${token}`
    
        const [url, setUrl] = useState(FeedInstagram);
        const [post, setPost] = useState([]);
        const [loading, setLoading] = useState(false);
        const [page, setPage] = useState(undefined);

        let siguiente = useRef(),
        anterior = useRef();
    
        useEffect(() => {
            const Post = async () => {
                setLoading(true);
                let res = await axios.get(url),
                    data = await res.data,
                    pagina = await data.paging,
                    json = await data.data,
                    next = pagina.next,
                    prev = pagina.previous;
                setPost(json);
                setLoading(false)
                setPage(pagina)
                prev===undefined
                    ?anterior.current.style.backgroundColor="#777"
                    :anterior.current.style.backgroundColor="#4558BD"
                next===undefined
                    ?siguiente.current.style.backgroundColor="#777"
                    :siguiente.current.style.backgroundColor="#4558BD"
                }
                Post()
            }, [url]);
            const Anterior = () => {
                page.previous!==undefined&&setUrl(page.previous)
            }
            const Siguiente = () => {
                page.next!==undefined&&setUrl(page.next)
            }
        return (
            <>
                <Data post={post} loading={loading} />
                <div className='butons'>
                    <Link to='up' smooth='true' duration={1000}>
                        <button onClick={Anterior} ref={anterior} className='previous botones'>Anterior</button>
                    </Link>
                    <Link to='up' smooth='true' duration={1000}>
                        <button onClick={Siguiente} ref={siguiente} className='next botones'>Siguiente</button>
                    </Link>
                </div>
            </>
        )
}    

export default Peticion;
