import './Card.css'
import Swal from 'sweetalert2'
import { Zoom } from 'react-reveal';

function Card(props) {

    function handleClick(e){
        Swal.fire({
            title: 'Grafica | FyC',
            text: `${props.titulo}`,
            imageUrl: `${props.img}`,
            imageWidth: '450px',
            confirmButtonText: 'Cerrar',
            confirmButtonColor: '#4558BD'
        });

}

    return (  
        <Zoom>
            <div className='card'>
                <img className='img-feed' loading='lazy' id={props.id} src={props.img} alt='imagen,instagram'/>
                <section className='describ'>
                    <h4 className='titulo-img'>{props.titulo}</h4>
                    <p className='fecha-img'>{props.fecha}</p>
                    <p className='max' onClick={handleClick} >Maximizar</p>
                </section>
            </div>
        </Zoom>
    );
}

export default Card;