import React from "react";
import Card from "./Card";
import Video from "./VideoCard";
import loader from '../recursos/tail-spin.svg'
import 'dayjs/locale/es'
import dayjs from "dayjs";

function Data({ post, loading }) {
    if (loading) {
        return <img 
            src={loader} 
            style={{width:'100px',margin:'auto'}} 
            alt='loader' />
    }
    return (
        <div> 
            {post.map((el)=>{
            return (
            <React.Fragment key={el.id} >
              { (el.media_type === 'IMAGE' ||  el.media_type === 'CAROUSEL_ALBUM') 
                && <Card 
                    key={el.id} 
                    titulo={el.caption} 
                    img={el.media_url} 
                    id={el.media_type}
                    url={el.permalink}
                    fecha={dayjs(el.timestamp).locale('es').format('D-MMMM-YYYY')} /> }
      
              { el.media_type === 'VIDEO' 
                && <Video 
                    key={el.id} 
                    titulo={el.caption} 
                    video={el.media_url} 
                    id={el.media_type}
                    fecha={dayjs(el.timestamp).locale('es').format('D-MMMM-YYYY')} /> } 
            </React.Fragment>
            );
        })}
        </div>
    );
}

export default Data;