import './Conocenos.scss'
import banner1 from '../recursos/imprenta-digital.jpg'
import banner2 from '../recursos/impresora.jpg'
import banner3 from '../recursos/impresora-2.jpg'
import banner4 from '../recursos/nuestra-grafica.jpg'
import RubberBand from 'react-reveal/RubberBand'
import { Slide, Zoom } from 'react-reveal'

function Conocenos() {
    return (  
        <div className="about">
            <div className='info-gral'>
                <RubberBand><h3>Conocenos un poco mas...</h3></RubberBand>
                <div className="info-der">
                    <Zoom><img className='img-xl' loading='lazy' src={banner1} alt='print,grafica' /></Zoom>
                    <div className='text'>
                        <Zoom left cascade><h4>Bienvenidos a Grafica F y C</h4></Zoom>                    
                        <p><i><Zoom bottom cascade>
                        Somos un proyecto familiar enfocado principalmente en la publicidad visual a 
                        través de la cartelería, folletería y publicidad digital, en la cual usted 
                        puede encontrar un beneficio por el cual incrementar su trascendencia productiva 
                        o de ventas. Ofrecemos calidad y rapidez en todas nuestras áreas de trabajo siempre 
                        teniendo en cuenta la necesidad de cada cliente. Como puede notar en nuestra web podrá 
                        encontrar todos nuestros trabajos realizados hasta la fecha, brindando garantía en la 
                        labor descripta anteriormente. 
                        </Zoom></i></p>                       
                    </div>
                    <Slide bottom><img className='img-xl' loading='lazy' src={banner3} alt='print,grafica' /></Slide>
                </div>
                <div className="info-cruzada">
                    <Slide left><img className='img-m' loading='lazy' src={banner2} alt='print,grafica' /></Slide>
                    <div className='texto'>
                        <Zoom right cascade><h4>Producción de calidad.</h4></Zoom>                        
                        <p><i><Zoom right cascade>
                        Contamos con maquinas de ultimo rodaje en el país, 
                        pudiendo así brindar una calidad de impresión nítida 
                        y con colores llamativos en la cual llamarà la atención 
                        de cualquier persona que lo vea, nuestros materiales 
                        cuentan con verificación de calidad HP 
                        (una de las mejores marcas mundiales si se habla de impresión). 
                        Todos nuestros diseños son personalizados, es decir no volverá a 
                        encontrar un trabajo copia del suyo, usted tendrá una marca única. 
                        </Zoom></i></p>                        
                    </div>
                </div>
                <div className="info-cruzada1">
                    <Slide right><img className='img-m1' loading='lazy' src={banner4} alt='print,grafica' /></Slide>
                    <div className='texto1'>
                        <Zoom left cascade><h4>Servicio a su disposición.</h4></Zoom>                        
                        <p><i><Zoom left cascade>
                        Aunque contamos con atención al público, 
                        aportamos un servicio sin costo extra de visita a su locación para su comodidad. 
                        Esto también nos beneficia en sentido de trabajo ya que podremos 
                        ver con claridad la zona donde procesaremos la cartelería. 
                        No dude en contactarnos ya que esto podría ser una oportunidad de 
                        renovar su negocio y darle una nueva imagen al público, los tiempos 
                        van cambiando y siempre se necesita estar al margen del presente.
                        </Zoom></i></p>                        
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Conocenos;