import { Flip } from 'react-reveal';
import './Galeria.scss'
import Peticion from './Peticion';


function Galeria() {

    return (  
        <div className="galeria">
            <Flip right><h2 id='up' className="head-title">NUESTROS TRABAJOS</h2></Flip>
            <div className="feed">
                <div id='loading' className='fotos'>
                    <Peticion/>
                </div>
            </div>
        </div>
    );
}

export default Galeria;